import TripPontsListView from '../view/trip-point-list-view.js';
import EditPointView from '../view/trip-point-edit-view.js';
import PointView from '../view/trip-point-view.js';
import { render, replace } from '../framework/render.js';
import { isEscapeKey } from '../utils/common.js';

/**
 * @description  ,    
 */
export default class TripPresenter {
  /**
   * @description DOM-,     
   * @type {HTMLElement}
   */
  #tripContainer = null;

  /**
   * @description   
   * @type {PointsModel}
   */
  #pointsModel = null;

  /**
   * @description   (ul)
   * @type {TripPontsListView}
   */
  #tripListView = new TripPontsListView();

  /**
   * @description   
   * @type {Array<Object>}
   */
  #tripPoints = [];

  /**
   * @param {Object} args -  
   * @param {HTMLElement} args.tripContainer - DOM-
   * @param {PointsModel} args.pointsModel -  
   */
  constructor({ tripContainer, pointsModel }) {
    this.#tripContainer = tripContainer;
    this.#pointsModel = pointsModel;
  }

  /**
   * @description  
   */
  init() {
    this.#tripPoints = this.#pointsModel.points;
    this.#renderTrip();
  }

  /**
   * @description    
   * @param {Object} point -  
   */
  #renderPoint(point) {
    const escKeyDownHandler = (evt) => {
      if (isEscapeKey(evt)) {
        evt.preventDefault();
        replaceFormToPoint();
        document.removeEventListener('keydown', escKeyDownHandler);
      }
    };

    const pointComponent = new PointView({
      point,
      onRollupClick: () => {
        replacePointToForm();
        document.addEventListener('keydown', escKeyDownHandler);
      }
    });

    const editComponent = new EditPointView({
      point,
      allOffers: this.#pointsModel.offers,
      allDestinations: this.#pointsModel.destinations,
      onFormSubmit: () => {
        replaceFormToPoint();
        document.removeEventListener('keydown', escKeyDownHandler);
      },

      onRollupClick: () => {
        replaceFormToPoint();
        document.removeEventListener('keydown', escKeyDownHandler);
      }
    });

    function replacePointToForm() {
      replace(editComponent, pointComponent);
    }

    function replaceFormToPoint() {
      replace(pointComponent, editComponent);
    }

    render(pointComponent, this.#tripListView.element);
  }

  /**
   * @description    
   */
  #renderTrip() {
    render(this.#tripListView, this.#tripContainer);

    for (let i = 0; i < this.#tripPoints.length; i++) {
      this.#renderPoint(this.#tripPoints[i]);
    }
  }
}
